/* src/app/my-draggable/my-draggable.component.css */
.draggable-box {
  width: 200px;
  height: 100px;
  background-color: transparent; /* Light blue */
  border: 2px solid #0288d1; /* Darker blue */
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: sans-serif;
  font-size: 1.2em;
  color: #333;
  cursor: grab; /* Changes cursor to indicate it's draggable */
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  position: absolute; /* Allows for free movement, or if you apply transformations */
  top: 100px; /* Initial position */
  left: 100px; /* Initial position */
  z-index: 100;

  /* Print styles */
  @media print {
    position: fixed; /* Changed from absolute to fixed for print */
    -webkit-print-color-adjust: exact; /* Force background colors in print */
    print-color-adjust: exact; /* Standard property */
    top: 100px;
    left: 100px;
  }

  content {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    pointer-events: none;
  }

  .resize-handle {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 16px;
    height: 16px;
    background: #0288d1;
    cursor: se-resize;
    border-top-left-radius: 4px;
  }
}

/* Optional: Add styles for when the element is being dragged */
.cdk-drag-preview {
  box-sizing: border-box;
  border-radius: 4px;
  box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2),
    0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12);
}

.cdk-drag-placeholder {
  opacity: 0; /* Or just don't display it if you don't want a gap */
}

.cdk-drag-animating {
  transition: transform 250ms cubic-bezier(0, 0, 0.2, 1);
}
